body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('./images/christian-lischka-taz4Xc32UCs-unsplash.jpg'); /*https://unsplash.com/photos/taz4Xc32UCs by Christian Lischka*/
  background-size: 100% 100%;
  box-shadow: inset 0 0 300px black;
  width: 100vw;
  height: 100vh;
}

.weather-app{
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


ul{
  list-style-type: none;
  margin: 0;
  padding: 0;
}

span{
  font-size: 0.8rem;
}

.card {
  animation:cardAnimation 500ms cubic-bezier(0.22, 0.61, 0.36, 1) both;
}

.row {
  overflow: auto;
  overflow-x: hidden;
}

@keyframes cardAnimation{
  from{
    transform: scale(0);
  }
  to{
    transform: scale(1);
  }
}